@import-normalize;
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body,
blockquote, figure,
form,
h1, h2, h3, h4, h5, h6,
ol, ul, p, pre {
  margin: 0;
}

body {
  font-size: 2rem;
}

img {
  max-width: 100%;
}

ul {
  padding-left: 0;
}

li {
  list-style: none;
}

button {
  border: 0;
  background: unset;
  color: currentColor;
  cursor: pointer;
}

#svg-sprite {
  display: none;
}

:root {
  --cMain: hsl(0, 0%, 100%);
  --cAccentCyan: hsl(176, 68%, 64%);
  --cAccentBlue: hsl(198, 60%, 50%);
  --cAccentError: hsl(0, 100%, 63%);
  --cBgHero: hsl(217, 28%, 15%);
  --cBgMain: hsl(218, 28%, 13%);
  --cBgFooter: hsl(216, 53%, 9%);
  --cBgTestimonial: hsl(219, 30%, 18%);
  --sp-sides: 4%;
}

.icon {
  position: relative;
  width: 3rem;
  height: 0;
  padding-top: 3rem;
  display: inline-block;
  vertical-align: middle;
}
.icon svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#footer .email .hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

body {
  color: var(--cMain);
  background-color: var(--cBgMain);
  font-family: "Open Sans", sans-serif;
  line-height: 3.2rem;
}

section {
  padding: 1em var(--sp-sides);
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

h1 {
  font-size: 4rem;
  line-height: 7rem;
}

a {
  color: var(--cMain);
  text-decoration: none;
}
@media (hover: hover) and (pointer: fine) {
  a:hover {
    text-decoration: underline;
  }
}

p {
  padding: 1em 0;
}

.btn {
  display: inline-block;
  margin: 2rem auto;
  padding: 1em 2em;
  background: linear-gradient(to right, var(--cAccentCyan), var(--cAccentBlue) 70%);
  border-radius: 10rem;
  font-family: "Raleway", sans-serif;
  font-size: 2.2rem;
  font-weight: 700;
  text-align: center;
}
.btn:focus, .btn:active {
  background: var(--cAccentCyan);
}
@media (hover: hover) and (pointer: fine) {
  .btn:hover {
    background: var(--cAccentCyan);
  }
}

.flex {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .flex {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
  }
}

.l-hero {
  text-align: center;
  background: var(--cBgHero) url("images/bg-curvy-mobile.svg") bottom/150% 50% no-repeat;
}
@media (min-width: 768px) {
  .l-hero {
    background-image: url("images/bg-curvy-desktop.svg");
  }
}
.l-hero header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-hero .icon {
  width: 17.6rem;
  padding-bottom: 5.2rem;
}
.l-hero nav a {
  margin-left: 1.5em;
}
.l-hero__content {
  max-width: 70rem;
  margin: 2em auto;
  font-size: 2.2rem;
}
.l-hero .btn {
  min-width: 40rem;
}

.info__icons {
  margin: 0 auto;
  gap: 5em;
  align-items: center;
  text-align: center;
}
.info__icons article {
  max-width: 50rem;
}
.info__icons img {
  height: 11.5rem;
  margin: 5rem auto;
}
.info article.flex {
  width: 100%;
  margin-top: 5em;
  gap: 5em;
}
@media (min-width: 768px) {
  .info article.flex {
    align-items: center;
    justify-content: space-between;
  }
}
.info article.flex a {
  padding-bottom: 0.5em;
  border-bottom: 2px solid currentColor;
  color: var(--cAccentCyan);
}
.info article.flex a:hover {
  color: var(--cMain);
}
.info article.flex i {
  margin-left: 0.5em;
}
.info article.flex div {
  flex: 1 1 30rem;
}

.testimonials {
  margin-top: 10rem;
  gap: 2em;
}
.testimonials figure {
  padding: 2em 2em 2em 3em;
  background-color: var(--cBgTestimonial);
  border-radius: 1rem;
  box-shadow: 3px 3px 6px var(--cBgFooter);
}
@media (min-width: 768px) {
  .testimonials figure {
    flex: 1 1 30rem;
  }
}
.testimonials blockquote {
  position: relative;
}
.testimonials blockquote:before {
  content: "";
  display: block;
  position: absolute;
  top: -1rem;
  left: -4rem;
  width: 3.5rem;
  height: 2.5rem;
  background-image: url("images/bg-quotes.png");
  background-size: cover;
}
.testimonials footer {
  margin-top: 1em;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.testimonials img {
  width: 5rem;
  margin-right: 1em;
  border-radius: 50%;
}
.testimonials cite {
  font-style: normal;
  flex: 1;
  line-height: 1.2em;
}

#footer {
  margin-top: 25rem;
  padding: 3em var(--sp-sides);
  position: relative;
  gap: 3em;
  flex-direction: column;
  flex-wrap: wrap;
  background: var(--cBgFooter);
}
@media (min-width: 768px) {
  #footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: unset;
  }
}
#footer .email {
  position: absolute;
  width: 95%;
  top: -15%;
  left: 50%;
  transform: translateX(-50%);
  padding: 2em;
  background: var(--cBgHero);
  border-radius: 1rem;
  box-shadow: 3px 3px 6px var(--cBgFooter);
  text-align: center;
}
@media (min-width: 768px) {
  #footer .email {
    max-width: 70rem;
    top: -20%;
  }
}
#footer .email form {
  position: relative;
  width: 100%;
  gap: 2em;
}
#footer .email input {
  flex: 1 1 100%;
  max-height: 5em;
  padding: 1em 2em;
  border: 0;
  border-radius: 10rem;
  font-size: 2rem;
}
#footer .email .btn {
  margin: 0;
  width: 100%;
}
#footer .brand {
  margin-top: 25rem;
  width: 100%;
}
#footer > p i {
  padding-right: 1em;
}
#footer .icon {
  width: 35.2rem;
  padding-bottom: 10.4rem;
}
#footer li {
  padding: 0.5em 0;
}
#footer .social {
  text-align: center;
  align-self: center;
}
#footer .social li {
  margin: 0 0.2em;
  display: inline-block;
}
#footer .social li a {
  display: block;
  width: 3.5em;
  padding: 1em;
  border: 1px solid currentColor;
  border-radius: 50%;
}
#footer .social li a:hover {
  color: var(--cAccentCyan);
}

/* Main layout, semantic tags. Use ID and classes prefixed as l- */
.copy {
  width: 100%;
  padding: 0 4%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 2em;
  font-size: smaller;
  background: white;
}
.copy, .copy a {
  color: black;
}
.copy p {
  padding: 0;
}
.copy li {
  padding-left: 0.5em;
  display: inline-block;
}
.copy i.fa-linkedin {
  color: #0077b5;
}